      ref="ruleFormRef"
      :model="ruleForm"
      :rules="rules"
    >
      <el-form-item prop="inviteCode">
        <el-input class="ahab-input" v-model="ruleForm.inviteCode" :placeholder="t('login.inviteCode')"/>
      </el-form-item>
